/* WebView.module.less */
.webviewContainer {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: white;
  z-index: 1000;
}

.webviewFrame {
  width: 100%;
  height: 100%;
  border: none;
}

.closeButton {
  position: absolute;
  top: 70px; /*   */
  right: 120px;
  z-index: 1001;
  /*    */
  width: 100px;
  height: 100px;
  /*     */
  background-color: rgba(0, 0, 0, 0.5);
  border: none;
  border-radius: 50%;
  /*    */
  font-size: 30px;
  color: white;
  /*   */
  &:hover {
    background-color: rgba(0, 0, 0, 0.7);
  }
}

@__DEV__: true;