/* FloatingButton.module.css */

.fab {
  position: fixed;
  bottom: 20px;
  right: 20px;
  z-index: 1000;
  width: 120px;
  height: 120px;
  background-color: #6200ee; /*    */
  color: white; /*   */
  border-radius: 50%; /*   */
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.3); /*   */
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background-color 0.3s, transform 0.3s;
  border: none; /*    */
  /*   */
  &:hover {
    background-color: #3700b3;
    transform: scale(1.05);
  }
}

.popupContainer {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1001;
  background-color: rgba(0, 0, 0, 0.8);
  padding: 20px;
  border-radius: 10px;
}

.popupContent {
  display: flex;
  flex-direction: column;
  gap: 10px;
  color: white;
}

@__DEV__: true;